import React, {useEffect, useState} from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { RootState } from '../../../reducers/index'
import { useHistory } from "react-router-dom";
import {
    Grid,
    Button,
    IconButton,
    Typography,
} from '@material-ui/core';
import ReactLoading from "react-loading";
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import Link from '@mui/material/Link';
import { Line, BarChart, Bar, LineChart, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import { CustomTooltipForLine, CustomTooltipForBar } from '../../../components/CustomToolbar'
import CropSquareIcon from '@mui/icons-material/CropSquare';
import DetailTable from '../../../components/DetailTable'
import _ from 'lodash'
import AddIcon from '@mui/icons-material/Add';
import CheckIcon from '@mui/icons-material/Check';
import Menu from '@mui/material/Menu';
import Divider from '@mui/material/Divider';
import MenuItem from '@mui/material/MenuItem';
import { dateOptionMappings, memberTypeMappings, memberTypeColorMappings } from '../../../utils/tool'
import {
    handleLandingTab,
    unmount_details,
    fetch_tickets_details
} from '../../../actions/landingActions'
import { bannerMappings } from '../../../utils/tool'

interface Props {
    children: JSX.Element | JSX.Element[];
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        main: {
            display: 'flex',
            width: '100%',
            backgroundColor:'#F9F9F9'
        },
        mainContainer: {
            width:'70%',
            marginTop:'30px',
            marginBottom: '100px',
            backgroundColor:'#FFFFFF',
            borderRadius:10,
            boxShadow: '0px 0px 20px rgba(0, 115, 250, 0.1)',
            [theme.breakpoints.down('lg')]:
            {
                width:'85%'
            }            
        },
        titleContainer: {
            width:'100%',
            backgroundColor:'#FFFFFF',
            padding: '30px 60px 0px 60px',
            height:'120px'
        },
        cardContainer:{

        },
        h1: {
            fontSize:'28px',
            color:'#000',
            fontWeight: 600,
            fontFamily:'Noto sans'
        },
        h2: {
            fontSize:'14px',
            color:'#989898',
            fontWeight: 400,
            fontFamily:'Noto sans'   
        },
        tableNumberRateFont:{
            fontFamily: 'Noto Sans',
            fontSize: '14px',
            color: '#FE2D53'
        },
        recordFontSm:{
            fontFamily: 'Noto Sans',
            fontStyle: 'normal',
            fontWeight: 400,
            fontSize: '14px',
            lineHeight: '19px'
        },
        breadcrumbsButton: {
            '&:hover': {
                backgroundColor:'transparent',
                boxShadow:'none'
            },
        }
    })
);


const Ticket = (props:Props) :React.ReactElement<Props>  =>  {
    const classes = useStyles()
    const dispatch = useDispatch()
    const history = useHistory()
    const isLoadding = useSelector((state: RootState) => state.isLoading)
    const filterColumn = useSelector((state: RootState)=> state.landingFilter)
    const landingDetails = useSelector((state: RootState)=> state.landingDetails)

    const [activeLine, setActiveLine] = useState('')
    
    const barColor = ['#4A78DF','#96C6FF']
    const tableHeaders = ['JRG Members', 'Yuu', 'Anonymous', 'Yuu & JRG Members', 'Yuu & Anonymous']

    // for line chart
    const [lineCombination, setLineCombination] = useState<string[]>([])
    const [lineSelected, setLineSelected] = useState<string[]>([])
    const [lineAnchorEl, setLineAnchorEl] = React.useState<null | HTMLElement>(null);
    const lineOpen = Boolean(lineAnchorEl);
    const lineHandleClick = (event: React.MouseEvent<HTMLElement>) => {
        setLineAnchorEl(event.currentTarget);
    };
    const lineHandleClose = () => {
        setLineAnchorEl(null);
    };
    
    const handleLineSelected = (mbs: string) =>
    {
        const isExist = _.includes(lineSelected, mbs)
        if (isExist){ setLineSelected(_.sortBy(lineSelected.filter(x=> x!== mbs))) }
        else{setLineSelected(_.sortBy([...lineSelected,mbs]))}
    }

    const handleLineSelectedAll = () =>
    {
        if (lineSelected.length !== lineCombination.length)
        {
            setLineSelected(lineCombination)
        }
        else
        {
            setLineSelected([])
        }
    }

    useEffect(() =>
    {
        if (_.isEmpty(landingDetails) === false)
        {
            const keys = _.sortBy(_.keys(landingDetails.p2[0]).filter((x: any) => x !== 'date' && _.startsWith(x, '_') === false))
            setLineCombination(keys)
            setLineSelected(keys)
        }
    }, [landingDetails])

    useEffect(() =>
    {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
        dispatch(fetch_tickets_details(filterColumn.market, filterColumn.banner, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange[0], filterColumn.compareDateRange[1]))
    }, [])
    
    useEffect(() => {
        return () => {
            dispatch(unmount_details())
        };
    }, []);

    return(
        <Grid container direction='column' alignItems='center' className={classes.main}>
            {/* title and breadcurumbs */}
            <Grid item container alignItems='center' className={classes.titleContainer} direction='column'>
                <Grid item container direction='column' >
                    <Grid item container>
                        <Breadcrumbs
                            separator={<NavigateNextIcon fontSize="small" />}
                            aria-label="breadcrumb"
                        >
                            <Link underline="hover">
                                <Button onClick={() => { dispatch(handleLandingTab(0)); history.push('/dashboard') }} disableRipple className={classes.breadcrumbsButton}><Typography className={classes.h2} >{'Dashboard'}</Typography></Button>
                            </Link>
                            <Link
                                underline="hover"
                            >
                                <Button onClick={() => { dispatch(handleLandingTab(0)); history.push('/dashboard') }} disableRipple className={classes.breadcrumbsButton}><Typography className={classes.h2} >{'Master data'}</Typography></Button>
                            </Link>
                            <Link
                                underline="hover"
                            >
                                <Button disableRipple className={classes.breadcrumbsButton}><Typography className={classes.h2} style={{color:'#5A91FF'}}>{'JRG ID records details'}</Typography></Button>
                            </Link>
                        </Breadcrumbs>
                    </Grid>
                    <Grid item container justifyContent='space-between' alignItems='center' style={{ paddingLeft: '10px' }} >
                        <Grid item>
                            <Grid container  alignItems='center'>
                                <IconButton disableRipple style={{ backgroundColor: 'transparent', padding:0, marginRight:'20px'}} onClick={() => { dispatch(handleLandingTab(0)); history.push('/dashboard') }} >
                                    <ArrowBackIosIcon style={{color:'#000'}}/>
                                </IconButton>
                                
                                <Grid item >
                                    <Typography className={classes.h1}>{'Number of tickets details'}</Typography>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item>
                            <Grid container >
                                <Grid item style={{marginRight:'50px'}}>
                                    <Grid container alignItems='flex-end' direction='column'>
                                        <Grid item>
                                            <Typography style={{fontSize:'18px',fontFamily:'Noto sans', color:'#0E0F0F'}}>{`Banner : ${_.join(filterColumn.banner.map((x:any)=> bannerMappings[x]),' / ')}`}</Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item>
                                    <Grid container alignItems='flex-end' direction='column'>
                                        <Grid item>
                                            <Typography style={{fontSize:'18px',fontFamily:'Noto sans', color:'#0E0F0F'}}>{`Date : ${filterColumn.startDate} to ${filterColumn.endDate}`}</Typography>
                                        </Grid>
                                        <Grid item>
                                            <Typography style={{fontSize:'12px',fontFamily:'Noto sans', color:'#989898'}}>{`Compate : ${filterColumn.compareDateRange[0]} to ${filterColumn.compareDateRange[1]}`}</Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            {/* main container */}
            {isLoadding ?
                <Grid container justifyContent='center' alignItems='center' style={{ height: '70vh' }}>
                    <ReactLoading type={'bars'} color="#5A91FF" />
                </Grid>
                :
                <Grid item container alignItems='center' className={classes.mainContainer} direction='column'>
                    <Grid item container style={{ marginTop: '30px', padding: '0px 45px', width: '100%' }}>
                        <Grid item lg={6} md={6}>
                            <Grid item container direction='column' className={classes.cardContainer} >
                                <Grid container style={{ marginTop: '30px' }}>
                                    <Grid item >
                                        <Typography style={{ fontSize: '18px', fontFamily: 'Noto sans' }} >{'Number of tickets details'}</Typography>
                                    </Grid>
                                </Grid>
                                <Grid container alignItems='center' style={{ marginTop: '18px' }}>
                                    {[dateOptionMappings[filterColumn.period], 'Preceding period'].map((ty, index) =>
                                    {
                                        return (
                                            <Grid item key={ty}>
                                                <Grid container direction='row' alignItems='center' style={{ marginRight: '20px' }}>
                                                    <CropSquareIcon style={{ width: '10px', height: '10px', marginRight: '8px', color: barColor[index], backgroundColor: barColor[index] }} />
                                                    <Grid item>
                                                        <Typography style={{ fontSize: '14px', fontFamily: 'Noto sans' }}>{ty}</Typography>
                                                    </Grid>
                                                </Grid>
                                            </Grid>
                                        )
                                    })}
                                </Grid>
                                <Grid container style={{ width: '100%', height: '240px', marginTop: '20px', marginLeft: '-20px' }}>
                                    <ResponsiveContainer width="100%" height="100%">
                                        <BarChart
                                            width={700}
                                            height={200}
                                            data={landingDetails.p1}
                                            margin={{
                                                top: 5,
                                                right: 30,
                                                left: 20,
                                                bottom: 5,
                                            }}
                                        >
                                            <CartesianGrid vertical={false} />
                                            <XAxis dataKey="key" tick={{ color: '#989898', fontFamily: 'Roboto', fontWeight: 300, fontSize: '14px' }} />
                                            <YAxis tick={{ color: '#989898', fontFamily: 'Roboto', fontWeight: 300, fontSize: '14px' }} />
                                            <Bar dataKey="value" fill="#4A78DF" barSize={20} />
                                            <Bar dataKey="lastValue" fill="#96C6FF" barSize={20} />
                                            <Tooltip content={(payload) => CustomTooltipForBar(payload, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange)} cursor={{ fill: 'rgba(14, 15, 15, 0.1)' }} />
                                        </BarChart>
                                    </ResponsiveContainer>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item lg={6} md={6}>
                            <Grid container direction='column' className={classes.cardContainer}>
                                <Grid container justifyContent='space-between' style={{ marginTop: '30px' }}>
                                    <Grid item >
                                        <Typography style={{ fontSize: '18px', fontFamily: 'Noto sans' }} >{'Number of tickets over a period of time'}</Typography>
                                    </Grid>
                                    <Grid item >
                                        <Button
                                            startIcon={<AddIcon style={{ color: '#5A91FF' }} />}
                                            variant='outlined'
                                            style={{ width: '150px', height: '32px', border: '1px dashed #5A91FF' }}
                                            id="demo-positioned-button"
                                            aria-controls="demo-positioned-menu"
                                            aria-haspopup="true"
                                            aria-expanded={lineOpen ? 'true' : undefined}
                                            onClick={lineHandleClick}
                                        >
                                            <Typography style={{ textTransform: 'none', fontSize: '16px', color: '#5A91FF', fontFamily: 'Noto sans' }}>{'Comparison'}</Typography>
                                        
                                        </Button>
                                        <Menu
                                            id="demo-positioned-menu"
                                            aria-labelledby="demo-positioned-button"
                                            anchorEl={lineAnchorEl}
                                            open={lineOpen}
                                            onClose={lineHandleClose}
                                            anchorOrigin={{
                                                vertical: 'top',
                                                horizontal: 'left',
                                            }}
                                            transformOrigin={{
                                                vertical: 'top',
                                                horizontal: 'left',
                                            }}
                                            MenuListProps={{
                                                'aria-labelledby': 'basic-button',
                                                style: { padding: 0, width: '200px' }
                                            }}
                                        >
                                            <MenuItem style={{ backgroundColor: lineSelected.length === lineCombination.length ? '#E2EEFF' : '' }} onClick={() => { handleLineSelectedAll() }}>
                                                <Grid container alignItems='center'>
                                                    {lineSelected.length === lineCombination.length ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                    <Grid item>
                                                        <Typography>{'All'}</Typography>
                                                    </Grid>
                                                </Grid>
                                            </MenuItem>
                                            <Divider style={{ width: '100%' }} />
                                            {lineCombination.map(bc =>
                                            {
                                                return <MenuItem key={bc} style={{ backgroundColor: _.includes(lineSelected, bc) ? '#E2EEFF' : '' }} onClick={() => { handleLineSelected(bc) }}>
                                                    <Grid container alignItems='center'>
                                                        {_.includes(lineSelected, bc) ? <CheckIcon style={{ width: '18px', height: '18px', marginRight: '8px', color: '#5A91FF' }} /> : <Grid style={{ width: '18px', height: '18px', marginRight: '8px' }}></Grid>}
                                                        <Grid item>
                                                            <Typography>{memberTypeMappings[bc]}</Typography>
                                                        </Grid>
                                                    </Grid>
                                                </MenuItem>
                                            })}
                                        
                                        </Menu>
                                    </Grid>
                                </Grid>
                                <Grid container justifyContent='space-between' style={{ marginTop: '18px' }}>
                                    <Grid item >
                                        <Grid container alignItems='center'>
                                            {lineSelected.map((ty) =>
                                            {
                                                return (
                                                    <Grid item key={ty}>
                                                        <Grid container direction='row' alignItems='center' style={{ marginRight: '12px' }}>
                                                            <CropSquareIcon style={{ width: '10px', height: '10px', marginRight: '8px', color: memberTypeColorMappings[ty], backgroundColor: memberTypeColorMappings[ty] }} />
                                                            <Grid item>
                                                                <Typography style={{ fontSize: '12px', fontFamily: 'Noto sans' }}>{memberTypeMappings[ty]}</Typography>
                                                            </Grid>
                                                        </Grid>
                                                    </Grid>
                                                )
                                            })}
                                        </Grid>
                                    </Grid>
                                
                                </Grid>
                                <Grid container alignItems='center' style={{ width: '100%', height: '240px', marginTop: '20px', marginLeft: '-20px' }}>
                                    <ResponsiveContainer width="100%" height="100%">
                                        <LineChart
                                            width={500}
                                            height={300}
                                            data={landingDetails.p2}
                                            margin={{
                                                top: 5,
                                                right: 30,
                                                left: 20,
                                                bottom: 5,
                                            }}
                                        >
                                            <CartesianGrid vertical={false} />
                                            <XAxis dataKey="date" tick={{ color: '#989898', fontFamily: 'Roboto', fontWeight: 300, fontSize: '14px' }} />
                                            <YAxis tick={{ color: '#989898', fontFamily: 'Roboto', fontWeight: 300, fontSize: '14px' }} />
                                            <Tooltip cursor={{ fill: '#0F0F0F', opacity: '10%' }} content={(payload) => CustomTooltipForLine(payload, memberTypeColorMappings, filterColumn.startDate, filterColumn.endDate, filterColumn.compareDateRange, activeLine)} />
                                            {
                                                lineSelected.map((dataKey, index) =>
                                                {
                                                    return (
                                                        <React.Fragment key={dataKey}>
                                                            <Line type="linear" name={memberTypeMappings[dataKey]} dataKey={dataKey} stroke={memberTypeColorMappings[dataKey]} strokeWidth={'3px'} style={{ opacity: (activeLine !== dataKey && activeLine !== '') ? 0.2 : 1 }} dot={false} activeDot={{ r: 5 }} onMouseEnter={() => setActiveLine(dataKey)} onMouseLeave={() => setActiveLine('')} />
                                                            <Line type="linear" name={`_${ memberTypeMappings[dataKey] }`} dataKey={`_${ dataKey }`} stroke={memberTypeColorMappings[dataKey]} strokeWidth={'1px'} strokeDasharray="2 2" dot={false} style={{ opacity: (activeLine !== `_${ dataKey }` && activeLine !== '') ? 0.2 : 1 }} onMouseEnter={() => setActiveLine(`_${ dataKey }`)} onMouseLeave={() => setActiveLine('')} />
                                                        </React.Fragment>
                                                    )
                                                })
                                            }
                                        </LineChart>
                                    </ResponsiveContainer>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    {/* statistic area */}
                    <Grid item container style={{ marginTop: '40px', marginBottom: '40px', padding: '0px 45px', width: '100%' }}>
                        <DetailTable headers={tableHeaders} datas={landingDetails.statisticData} />
                    </Grid>
                </Grid>
            }
        </Grid>
    )
}

export default Ticket