import React from 'react';
import {
    Grid,
    Typography,
} from '@material-ui/core';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp'
import _ from 'lodash'
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import { currencyFormat } from '../utils/tool';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        main: {
            width: '100%'
        },
        headerFont: {
            fontFamily:'Noto sans',
            fontSize:'16px',
            fontWeight:'bold',
            color:'#0E0F0F'
        },
        bodyFont: {
            fontFamily:'Noto sans',
            fontSize:'16px',
            color:'#0E0F0F'
        }
    })
);

interface props {
    headers: string[]
    datas: any
}

const DetailTable = (props: props) =>  {

    const classes = useStyles()

    return(
        <Grid container direction={'column'} className={classes.main} >
            <Grid item style={{width:'100%',border:'1px solid #989898'}}>
                
            </Grid>
            {/* header */}
            <Grid item container alignItems='center' justifyContent='center' style={{height:'50px'}}>
                <Grid item style={{width:'20%'}}>
                    <Grid container justifyContent='flex-end'>
                        <Grid item style={{height:'50px',width:'1px',border:'1px solid #989898'}}></Grid>
                    </Grid>
                </Grid>
                {
                    props.headers.map(col =>
                    {
                        return (
                            <Grid item lg md key={col}>
                                <Grid container justifyContent='center'>
                                    <Typography className={classes.headerFont}>{col}</Typography>
                                </Grid>
                            </Grid>
                        )
                    })
                }
            </Grid>
            <Grid item style={{width:'100%',border:'1px solid #989898'}}>
                
            </Grid>
            {/* table body */}
            {_.keys(props.datas).map((banner: any, index) =>
            {
                const period = props.datas[banner][0].period
                const comparePeriod = props.datas[banner][1].period
                
                const periodValue = _.keys(props.datas[banner][0]).filter(x => x !== 'period').map(y => props.datas[banner][0][y]) as number[]
                const comparePeriodValue = _.keys(props.datas[banner][1]).filter(x => x !== 'period').map(y => props.datas[banner][1][y]) as number[]
                
                const changeValue = periodValue.map((v: number, index: number) =>
                {
                    const value = _.floor(_.divide((v - comparePeriodValue[index]), comparePeriodValue[index]) * 100, 2)
                    const finalValue = _.isNaN(value) ? '0' : !_.isFinite(value) ? '100' : value
                    return finalValue
                })

                const borderBottomColor = index === _.keys(props.datas).length - 1 ? '2px solid #989898' : '1p solid rgba(0, 0, 0, 0.06)'
                
                return(
                <Grid item  container direction='column' style={{backgroundColor:index%2===0? '#FFF': '#F1F7FF'}} key={banner}>
                    <Grid item container style={{height:'50px',borderBottom:'1px solid rgba(0, 0, 0, 0.06)'}}>
                        <Grid item style={{width:'20%'}}>
                            <Grid container justifyContent='space-between' alignItems='center'>
                                <Grid item>
                                    <Typography className={classes.headerFont}>{`${index}. ${banner}`}</Typography>
                                </Grid>
                                <Grid item style={{height:'50px',width:'1px',border:'1px solid #989898'}}></Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item container alignItems='center' justifyContent='center' style={{height:'50px',borderBottom:'1px solid rgba(0, 0, 0, 0.06)'}}>
                        <Grid item style={{width:'20%'}}>
                            <Grid container justifyContent='space-between' alignItems='center'>
                                <Grid item>
                                    <Typography>{period}</Typography>
                                </Grid>
                                <Grid item style={{height:'50px',width:'1px',border:'1px solid #989898'}}></Grid>
                            </Grid>
                        </Grid>
                        {
                            periodValue.map((value: any) =>
                            {
                                return (
                                    <Grid item lg md key={value} >
                                        <Grid container justifyContent='center'>
                                            <Typography className={classes.bodyFont}>{currencyFormat(value)}</Typography>
                                        </Grid>
                                    </Grid>
                                )
                            })
                        }   
                    </Grid>
                    <Grid item container alignItems='center' justifyContent='center' style={{height:'50px',borderBottom:'1px solid rgba(0, 0, 0, 0.06)'}}>
                        <Grid item style={{width:'20%'}}>
                            <Grid container justifyContent='space-between' alignItems='center'>
                                <Grid item>
                                    <Typography>{comparePeriod}</Typography>
                                </Grid>
                                <Grid item style={{height:'50px',width:'1px',border:'1px solid #989898'}}></Grid>
                            </Grid>
                        </Grid>
                        {
                            comparePeriodValue.map((value: any) =>
                            {
                                return (
                                    <Grid item lg md key={value}>
                                        <Grid container justifyContent='center'>
                                            <Typography className={classes.bodyFont}>{currencyFormat(value)}</Typography>
                                        </Grid>
                                    </Grid>
                                )
                            })
                        }   
                    </Grid>
                    <Grid item container  alignItems='center' justifyContent='center' style={{height:'50px',borderBottom:borderBottomColor}}>
                        <Grid item style={{width:'20%'}}>
                            <Grid container justifyContent='space-between' alignItems='center'>
                                <Grid item>
                                    <Typography>{'Change(%)'}</Typography>
                                </Grid>
                                <Grid item style={{height:'50px',width:'1px',border:'1px solid #989898'}}></Grid>
                            </Grid>
                        </Grid>
                        {
                            changeValue.map((value: any) =>
                            {
                                return (
                                    <Grid item lg md key={value}>
                                        <Grid container justifyContent='center' alignItems='center'>
                                            <Typography className={classes.bodyFont} style={value >= 0 ? { color: '#52C41A' } : { color: '#FE2D53' }}>{`${Math.abs(value)}%`}</Typography>                       
                                             {
                                                value >= 0 ?    
                                                <ArrowDropUpIcon style={{color:'#52C41A'}}/>:

                                                <ArrowDropDownIcon style={{color:'#FE2D53'}}/>
                                            }
                                        </Grid>
                                    </Grid>
                                )
                            })
                        }
                    </Grid>
                </Grid>
                )
            })}
        </Grid>
    )
}


export default DetailTable